.slider{
  position: relative;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button {
      margin: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  &__wrap{
    position: relative;
    overflow: hidden;
  }
}

.swiper-pagination{
  position: static;
  width: 100%;
  margin-top: 1.25em;
  &-bullet{
    background-color: $grayActive;
    opacity: 1;
    width: .625em;
    height: .625em;
    margin: 0 .3125em !important;
    &-active{
      background-color: $primary;
    }
  }
}

.swiper-button{
  width: 2.125em;
  height: auto;
  @include center;
  border-radius: .25em;
  background-color: $white;
  transition: all .3s;
  svg{
    width: 100%;
  }
  &:hover{
    background-color: $primary;
    path{
      fill: $white;
    }
  }
}

.category-slider{
  padding: 0 4em;
  .slider__wrap{
    padding: 1.5em 2.75em 1.875em;
  }
  .swiper{
    &-container{
      width: calc(100% + 3em);
    }
    &-slide{
      width: calc(100% / 7);
      padding-right: 3em;
    }
    &-button{
      &-prev{
        left: 1.25em;
      }
      &-next{
        right: 1.25em;
      }
    }
  }
}

.category-card{
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-weight: 600;
  padding: 1.25em .25em;
  border-radius: .625em;
  &:hover{
    color: $black;
    box-shadow: $shadow;
  }
  &__icon{
    height: 4.375em;
    flex-shrink: 0;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    margin-bottom: .8125em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.m-section{
  padding: 3.125em 0;
}

.m-section2{
  padding: 2.5em 0;
}


