.for-designers{
  margin-top: 1.875em;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__link{
    text-decoration: underline;
    color: $primary;
    margin-bottom: .375em;
  }
  &__text{
    a{
      color: $black;
      &:hover{
        color: $primary;
      }
    }
  }
}

.guarantee{
  margin-top: 1.875em;
  padding: .625em 1.25em;
  border: $border;
  border-radius: .625em;
  display: flex;
  align-items: flex-start;
  &__icon{
    align-self: center;
    width: 3.6875em;
    margin-right: 1.25em;
  }
  &__title{
    font-size: .875em;
    line-height: 120%;
  }
  &__year{
    font-size: 1.5em;
    line-height: 125%;
    font-weight: 700;
    letter-spacing: 0.01em;
  }
  &__desc{
    font-size: .75em;
    line-height: 120%;
    font-weight: 700;
  }
  .m-link{
    margin-left: auto;
    span{
      font-size: .625em;
    }
    svg{
      width: .625em;
    }
  }
}

.props{
  margin-top: 1.875em;
  @include grid100gap(.5em);
}

.catalog-props,
.catalog-desc{
  &._show-all{
    .more-btn{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      svg{
        transform: rotate(-180deg);
      }
    }
  }
}

.catalog-props{
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 3.4375em));
    gap: .5em 6.875em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.875em;
  }
  &:not(._show-all){
    .prop-item{
      &:nth-child(8)~.prop-item{
        display: none;
      }
    }
  }
}

.more-btn{
  display: flex;
  align-items: center;
  height: 2.125em;
  color: $grayDark;
  &:hover{
    color: $primary;
    path{
      fill: $primary;
    }
  }
  span{
    font-size: 1em;
  }
  svg{
    width: .875em;
    margin-left: .625em;
  }
  ._view2{
    display: none;
  }
}

.catalog-desc{
  &__text{
    margin-top: 1.25em;
    p{
      font-size: 1.1875em;
      line-height: 120%;
      @include lastM(0 0 .5em);
    }
    hr{
      margin: 0;
      display: block;
      opacity: 0;
      height: .6875em;
    }
    ol{
      margin-bottom: 1em;
    }
  }
  &__nav{
    margin-top: .5em;
  }
  &:not(._show-all){
    .catalog-desc__text{
      p:nth-child(1)~*{
        display: none;
      }
    }
  }
  &__info{
    padding: .625em .625em .625em 1.25em;
    border-radius: .625em;
    border: $border;
    display: flex;
    align-items: center;
    max-width: 43.75em;
    img{
      width: 2.3125em;
      margin-right: 1.25em;
    }
  }
}
