.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background: rgba(0, 0, 0, 0.3);
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 25.125em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 1.875em 2.5em;
        border-radius: 1.25em;
        display: inline-block;
    }
    &__title{
        text-align: center;
        text-transform: unset;
        margin-bottom: .77em;
    }
    .close-btn{
        position: absolute;
        top: -1.5625em;
        right: -1.5625em;
    }
}

.close-btn{
    width: 3.125em;
    height: 3.125em;
    border-radius: 50%;
    @include center;
    flex-shrink: 0;
    img{
        width: 1.375em;
    }
}

.modal-form{
    &__hint{
        padding: 1.25em 1em 0;
        a{
            text-decoration: underline;
        }
    }
    &__btn{
        width: 100%;
        margin-top: 1.25em;
        border-radius: .5em;
        height: 2.5em;
        span{
            font-size: .875em;
        }
    }
}



