.popular{
  .tabs{
    margin-top: 1.125em;
    justify-content: center;
  }
  .tab-content{
    margin-top: 1.875em;
  }
  .product-collection{
    margin-top: 1.875em;
  }
}

.product-collection{
  @media screen and (min-width: 993px){
    &:not(._dynamic){
      .swiper{
        &-wrapper{
          display: grid;
          grid-template-columns: repeat(4,calc(25% - 1.5em));
          gap: 2.25em 2em;
          transform: unset !important;
        }
        &-slide{
          width: 100% !important;
          position: relative;
          &:hover{
            z-index: 5;
          }
        }
      }
    }
    &._dynamic{
      .swiper{
        &-container{
          width: calc(100% + 2em);
        }
        &-slide{
          width: 25%;
          padding-right: 2em;
          opacity: 0;
          pointer-events: none;
          transition: all .3s;
          &-active,
          &-next,
          &-next+.swiper-slide,
          &-next+.swiper-slide+.swiper-slide{
            opacity: 1;
            pointer-events: unset;
          }
        }
        &-button{
          top: 5.8125em;
          &-prev{
            left: -3.375em;
          }
          &-next{
            right: -3.375em;
          }
          @media screen and (max-width: 1400px){
            &-prev{
              left: -2.375em;
            }
            &-next{
              right: -2.375em;
            }
          }
        }
      }
    }
  }
}

.product-card{
  position: relative;
  &__top{
    position: relative;
    z-index: 2;
  }
  &__img{
    @include imgCover(59%);
    border-radius: .625em;
  }
  &__action{
    position: absolute;
    top: .5em;
    right: .5em;
    transition: all .3s;
    z-index: 2;
    &-btn{
      margin-left: .3125em;
    }
  }
  &__sale{
    position: absolute;
    top: .625em;
    left: .625em;
    z-index: 2;
  }
  &__content{
    padding: .625em 1.25em 1.875em;
    margin-top: .625em;
    @include grid100gap(.625em);
    position: relative;
    z-index: 2;
  }
  &__arrow{
    width: 1em;
    @include center;
    margin-left: 1em;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  &__feedback{
    .rating{
      margin-right: .5em;
    }
  }
  &__price2{
    text-decoration: line-through;
    margin-left: .5em;
  }
  &__colors{
    position: relative;
    z-index: 4;
  }
  &__bottom{
    display: none;
  }
  &__buy{
    position: relative;
    z-index: 3;
  }
  &__props{
    width: 100%;
    margin-top: .75em;
    @include grid100gap(.5em);
  }
  .slider{
    overflow: hidden;
    border-radius: .625em;
    .swiper{
      &-button{
        width: 1.5em;
        &-prev{
          left: .25em;
        }
        &-next{
          right: .25em;
        }
        &:not(:hover){
          background-color: transparent;
          path{
            fill: $grayActive;
          }
        }
      }
    }
  }
  @media screen and (min-width: 993px){
    &:hover{
      z-index: 5;
      &:before{
        box-shadow: $shadow;
        opacity: 1;
      }
      .product-card__bottom{
        opacity: 1;
        pointer-events: unset;
      }
      .product-card__cart-btn,
      .product-card__arrow,
      .product-card__action{
        opacity: 1;
      }
    }
    &:before{
      content: '';
      position: absolute;
      left: -.375em;
      width: calc(100% + .75em);
      background-color: $white;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &:before{
      border-radius: .625em .625em 0 0;
      top: -.3125em;
      height: calc(100% + .3125em);
    }

    &__arrow,
    &__action,
    &__cart-btn{
      opacity: 0;
    }
    &__bottom{
      display: flex;
      flex-direction: column;
      align-items: center;
      position: absolute;
      padding: 0 1.625em 1em;
      width: calc(100% + .75em);
      top: 100%;
      left: -.375em;
      margin-top: -1.25em;
      box-shadow: $shadow;
      border-radius: 0 0 .625em .625em;
      background-color: $white;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      &:after{
        content: '';
        position: absolute;
        top: -1.5em;
        left: 0;
        background-color: $white;
        height: 3.125em;
        width: 100%;
      }
    }
  }
}

._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

.m-badge{
  background-color: $primary;
  padding: 0 .625em;
  font-weight: 800;
  height: 1.5625em;
  display: flex;
  align-items: center;
  border-radius: 1.25em;
  color: $white;
  span{
    font-size: .875em;
    letter-spacing: 0.01em;
  }
  &._green{
    background-color: $green;
  }
  &._red{
    background-color: $red;
  }
}

.rating {
  display: flex;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field {
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: .6875em;
    svg {
      width: 100%;
      height: auto;
    }
    &:hover {
      cursor: pointer;
      path {
        fill: $primary;
      }
      &~.rating-input__item {
        path {
          fill: $primary;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path {
    fill: $primary;
  }
}

.prop-item{
  display: flex;
  align-items: flex-end;
  &:not(.primary-text){
    color: $grayDark;
  }
  &:after{
    content: '';
    min-width: 1em;
    flex-grow: 1;
    border-bottom: 1px dashed $grayActive;
    margin: 0 .3125em .25em;
  }
  &__title,
  &__desc{
    font-size: .8125em;
    line-height: 124%;
    letter-spacing: 0.01em;
  }
  &__desc{
    order: 2;
  }
}


