.main-slider{
  &__nav{
    position: absolute;
    bottom: 1.5em;
    left: 0;
    z-index: 2;
    width: 100%;
  }
  .swiper{
    &-pagination{
      position: static;
      width: auto;
      display: flex;
      align-items: center;
    }
    &-button{
      background-color: transparent;
      width: 2.75em;
      path{
        fill: $white;
      }
      &:hover{
        path{
          fill: $primary;
        }
      }
      &-prev{
        left: 1em;
      }
      &-next{
        right: 1em;
      }
      @media screen and (min-width: 1700px){
        &-prev{
          left: 10em;
        }
        &-next{
          right: 10em;
        }
      }
    }
  }
}

.main-banner{
  overflow: hidden;
  &__inner{
    min-height: 25em;
    display: grid;
    grid-template-columns: 34.375em calc(100% - 35em);
    gap: .625em;
  }
  &__content{
    background: #f0f0f0;
    position: relative;
    padding: 2em 2em 4em 0;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 200em;
      height: 100%;
      background: inherit;
      z-index: -1;
    }
  }
  &__subtitle{
    font-size: 6em;
    line-height: 117%;
    text-transform: uppercase;
    font-weight: 100;
  }
  .h1{
    font-weight: 900;
  }
  &__img{
    height: 25em;
    width: calc(100vw - ((100vw - 1440px) / 2) - 34.375em);
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__nav{
    .m-btn{
      font-weight: 800;
    }
  }
}


