.order-form{
  margin-top: 3.5em;
  &__grid{
    margin-top: 2.25em;
  }
  &__check{
    display: flex;
    padding: 1.25em 0 0 1.25em;
  }
}

.order-requisites{
  margin-top: 1.875em;
  &__view{
    display: none;
    &._active{
      display: block;
    }
  }
}

.requisites-form{
  width: calc((100% / 3 - 1.75em / 3) * 2 + .875em);
  &__fields{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 4.375em / 6));
    gap: .875em;
    margin-top: 1.75em;
  }
  ._col2{
    grid-column: span 2;
  }
  ._col3{
    grid-column: span 3;
  }
  &__wrap{
    grid-column: 1/-1;
    display: flex;
    align-items: center;
    .fg{
      flex-grow: 1;
    }
    .m-check{
      margin-left: .875em;
      flex-shrink: 0;
    }
  }
}
