.tabs{
  display: flex;
}

.tab{
  height: 2.75em;
  padding: 0 1.25em;
  display: flex;
  align-items: center;
  position: relative;
  color: $grayDark;
  border-radius: .625em;
  transition: all .3s;
  margin: .3125em .75em 0 0;
  span{
    font-size: 1.1875em;
    letter-spacing: 0.02em;
  }
  &:hover{
   color: $primary;
    cursor: pointer;
  }
  &._active{
    background: $gray;
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
