.video{
  display: block;
  position: relative;
  &__play{
    @include centerAbsolute;
    width: 2.4375em;
  }
  &__img{
    border-radius: 1.875em;
    img{
      border-radius: inherit;
      width: 100%;
      display: block;
    }
  }
}

.process{
  &__text{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2.5em));
    gap: 5em;
  }
}

.faq{
  &__items{
    margin-top: 1.875em;
    @include grid100gap(.625em);
  }
}

.qa-card{
  padding: 1em 1.25em;
  border-radius: .625em;
  box-shadow: $shadow;
  background-color: $gray;
  transition: all .3s;
  &._active{
    background-color: $white;
    ._hide{
      opacity: 0;
    }
  }
  &__show{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    cursor: pointer;
  }
  &__hidden{
    margin-top: .375em;
    display: none;
  }
  &__btn{
    height: 1.3125em;
    width: 1em;
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}


.custom-req{
  color: $white;
  background-color: $black;
  margin-top: 2.5em;
  margin-bottom: -1.875em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 22.625em) 22.625em;
  }
  &__col{
    position: relative;
    min-height: 32.8125em;
    padding: 3em 1.75em 0 0;
  }
  &__img{
    border-bottom: 1px solid rgba(#fff,.3);
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: calc(100vw - ((100vw - 1440px) / 2) - 22.625em);
    &:after{
      background-color: rgba($black,.2);
    }
    img{
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  &__title{
    position: relative;
    z-index: 2;
    font-size: 2.75em;
    line-height: 123%;
    text-transform: uppercase;
    font-weight: 700;
    text-shadow: 0 .25em .5625em rgba(0, 0, 0, 0.35);
    span{
      display: block;
      line-height: inherit;
      &:nth-child(2){
        text-align: right;
      }
    }
  }
  &__col2{
    padding: 2.25em .625em 2em 2em;
  }
  .req-form__fields{
    margin-top: 0;
  }
}

.file-input{
  &__btn{
    border-radius: .5em;
    width: 100%;
    span{
      font-size: .875em;
    }
    svg{
      width: 1em;
      margin-right: .625em;
    }
    &:hover{
      path{
        fill: $black;
      }
    }
  }
  &__output{
    display: flex;
    align-items: center;
    padding: 1em .75em 0;
  }
  &__name,
  &__delete{
    font-size: .875em;
    color: $grayDark;
  }
  &__delete{
    margin-left: 1.875em;
    &:hover{
      color: $primary;
    }
  }
}
