.breadcrumbs {
    padding: .4375em;
    margin-bottom: 1.6875em;
    &:last-child{
        margin-bottom: 0;
    }
    li{
        position: relative;
        display: inline;
        color: $grayActive;
        &:not(:first-child) {
            margin-left: 1.5em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                top: .1875em;
                right: 100%;
                margin-right: .5em;
                width: .77em;
                height: .77em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
            }
        }
        &:first-child{
            max-width: .8125em;
            a{
                color: transparent;
                background-image: url(../img/home.svg);
                @include bgImgSize(100%);
                width: 1em;
                overflow: hidden;
                display: inline-block;
                transform: translateY(.25em);
            }
        }
        &:last-child{
            color: $grayDark;
            pointer-events: none;
        }
    }
    a{
        font-size: .8125em;
        line-height: 115%;
    }
}
