._mt60{
  margin-top: 3.75em;
}

.portfolio-item{
  padding-bottom: 2.5em;
}

.portfolio{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
  }
}

.portfolio-card2{
  &__img{
    clip-path: polygon(2.875em 0%, 100% 0, 100% 100%, 0 100%, 0% 2.875em);
    @include imgCover(51%);
    border-radius: 1.25em;
  }
  &__content{
    padding: .625em 1.875em 0;
    display: flex;
    justify-content: space-between;
  }
}

.req{
  margin: 0 0 -1.875em;
  padding: 4.375em 0 6.625em;
  background-size: cover;
  color: $white;
  .container{
    position: relative;
    z-index: 2;
  }
  &__inner{
    display: grid;
    grid-template-columns: 50.3125em 20em;
    justify-content: space-between;
  }
}

.fg{
  &._type2{
    input,textarea{
      background-color: $white;
      padding-top: 1.25em;
    }
    label{
      background-color: transparent;
    }
    input._filled~label,
    input:focus~label,
    select._filled~label,
    select:focus~label,
    textarea._filled~label,
    textarea:focus~label{
      top: .375em;
      transform: unset;
    }
  }
}

.req-form{
  &__fields{
    @include grid100gap(.9375em);
    margin-top: 2.25em;
  }
  &__btn{
    width: 100%;
    margin-top: 1.875em;
    border-radius: .5em;
    height: 2.5em;
    span{
      font-size: .875em;
    }
  }
  &__hint{
    margin-top: 1.25em;
    text-align: center;
    padding: 0 1.25em;
    color: $grayActive;
  }
}

.req-design{
  &__desc{
    margin-top: 2em;
    font-weight: 300;
    .text20{
      line-height: 140%;
    }
  }
  &__desc2{
    max-width: 29.375em;
    margin-top: 2.625em;
    .text16{
      line-height: 120%;
    }
  }
  &__desc3{
    max-width: 26.125em;
    margin-top: 3.125em;
    .text16{
      line-height: 120%;
    }
    span{
      -webkit-box-decoration-break: clone;
      box-decoration-break: clone;
      display: inline;
      background-color: $primary;
      padding: .2em;
      border-radius: .4em;
    }
  }
}
