.header{
  &__inner{
    display: flex;
  }
  &__logo{

  }
}

.header-top{
  padding: 1em 0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__phone{
    margin-left: 2.25em;
    font-weight: 600;
  }
  &__call{
    margin-left: 1.75em;
    display: flex;
    align-items: center;
    color: $primary;
    svg{
      width: .875em;
      margin-right: .25em;
    }
    span{
      text-decoration: underline;
    }
    &:hover{
      color: $black;
      path{
        fill: $black;
      }
    }
  }
  &__time{
    margin-left: 2em;
    font-weight: 600;
    color: $grayDark;
  }
}

.header-city{
  &__show{
    display: flex;
    align-items: center;
    color: $grayDark;
    font-weight: 600;
    &:hover{
      cursor: pointer;
      color: $primary;
      path{
        fill: $primary;
      }
    }
    span{
      font-size: .875em;
    }
    svg{
      width: 1.25em;
      margin-left: .25em;
    }
  }
}

.nav{
  margin-left: auto;
  color: $grayDark;
  .menu{
    display: flex;
    align-items: center;
  }
  li{
    margin-left: 1.25em;
  }
  a{
    font-weight: 600;
    font-size: .875em;
  }
}

.burger{
  display: none;
}
