.portfolio-slider2{
  margin-top: 1.875em;
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-slide{
      width: 50%;
      padding-right: 1.875em;
    }
    &-button{
      top: 10.3125em;
      &-prev{
        left: -3.375em;
      }
      &-next{
        right: -3.375em;
      }
    }
    &-pagination{
      margin-top: 1.875em;
    }
  }
}

.portfolio-gallery{
  margin-top: 1.25em;
}

.gallery{
  position: relative;
  overflow: hidden;
  &__img{
    @include imgCover(42%);
    border-radius: .625em;
  }
  &__big{
    position: relative;
  }
  &__small{
    margin-top: 1.875em;
    .swiper-slide{
      cursor: pointer;
      width: calc(100% / 6);
      &-thumb-active{
        .gallery__img {
          &:before {
            opacity: 1;
          }
        }
      }
    }
    .gallery__img{
      pointer-events: none;
      padding-bottom: 56.25%;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        border: 2px solid $primary;
        z-index: 2;
        transition: all .3s;
        opacity: 0;
      }
    }
  }
  .swiper{
    width: calc(100% + 2em);
    &-slide{
      padding-right: 2em;
    }
    &-pagination{
      position: absolute;
      margin: 0;
      bottom: 1.25em;
      z-index: 2;
    }
    &-button{
      width: 2.75em;
      background-color: transparent;
      path{
        fill: $white;
      }
      &-prev{
        left: 1.25em;
      }
      &-next{
        right: 1.25em;
      }
    }
  }
  &__badges{
    position: absolute;
    top: 1.25em;
    left: 1.25em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    z-index: 2;
    .m-badge{
      margin-bottom: .3125em;
    }
  }
  &__action{
    position: absolute;
    top: 1.25em;
    right: 1.25em;
    z-index: 2;
    .favourite-btn{
      margin-left: .3125em;
    }
  }
}

.portfolio-info{
  margin-top: 5em;
  display: grid;
  grid-template-columns: calc(100% - 14.375em) 13.125em;
  gap: 1.25em;
  &__desc{
    margin-top: 3.75em;
    &:first-child{
      margin-top: 0;
    }
    p{
      font-size: .875em;
    }
  }
}

.portfolio-props{
  padding: .625em 0 1.5em 1em;
  border-left: 1px solid #d9d9d9;
  @include grid100gap(1.875em);
  &__item{
    display: flex;
  }
  &__icon{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: .4375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.25em);
  }
  &__title{
    font-size: .8125em;
    line-height: 123%;
    letter-spacing: 0.01em;
    color: $grayActive;
  }
  &__desc{
    font-size: 1.125em;
    letter-spacing: 0.01em;
    font-weight: 700;
  }
}
