.selected-filters{
  display: flex;
  flex-wrap: wrap;
  margin-top: .5em;
  &__item,
  &__clear{
    margin: .625em .625em 0 0;
    height: 2.25em;
    display: flex;
    align-items: center;
  }
  &__item{
    padding: 0 1.25em;
    border-radius: 2.5em;
    background-color: $primary;
    color: $white;
    &-text{
      font-size: .875em;
    }
    &-remove{
      width: 1em;
      @include center;
      margin-left: .5em;
      flex-shrink: 0;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__clear{
    border-radius: 2.125em;
    padding: 0 .9375em;
    span{
      font-size: .875em;
    }
  }
}

.filter2{
  &__groups{
    @include grid100gap(1.25em);
  }
  &__bottom{
    margin-top: 1.875em;
    display: flex;
    justify-content: center;
    .m-btn{
      height: 1.875em;
    }
  }
}

.filter2-group{
  border-radius: .625em;
  transition: all .3s;
  &._active{
    padding-top: .5em;
    background-color: $gray;
    .filter2-group__btn{
      svg{
        transform: rotate(-180deg);
      }
    }
  }
  &._static{
    background-color: transparent;
    .filter2-group__btn{
      display: none;
    }
    .filter2-group__top{
      pointer-events: none;
    }
    .filter2-group__bottom{
      padding-top: .625em;
      padding-bottom: .625em;
      display: block !important;
    }
  }
  &:not(._show-all-checks){
    .m-check:nth-child(2)~.m-check{
      display: none;
    }
    .m-color:nth-child(3)~.m-color{
      display: none;
    }
    &._three{
      .m-check:nth-child(3){
        display: block;
      }
    }
    &._five{
      .m-check:nth-child(3),
      .m-check:nth-child(4),
      .m-check:nth-child(5){
        display: block;
      }
    }
  }
  &._show-all-checks{
    .filter2-group__more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  &__top{
    display: flex;
    align-items: center;
    padding: 0 1.25em;
    cursor: pointer;
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-weight: 700;
  }
  &__btn{
    width: 1em;
    background-color: $white;
    border-radius: .25em;
    @include center;
    margin-left: .75em;
    svg{
      width: 100%;
    }
  }
  &__bottom{
    padding: 1.25em 1.25em 1em;
    display: none;
  }
  &__checks{
    padding: 0 1.25em;
    @include grid100gap(.625em);
    &-line{
      width: calc(100% + 2.5em);
      height: 1px;
      background-color: $grayActive;
      margin: .625em 0 .625em -1.25em;
    }
  }
  &__more{
    margin-top: .875em;
    margin-left: 1.25em;
    ._view2{
      display: none;
    }
  }
  &__search{
    margin-bottom: 1em;
    .fg{
      input{
        height: 2.5em;
        border-radius: .71em;
        padding: 0 2.2em 0 .71em;
        background-color: $white;
        &::placeholder{
          color: $grayActive;
          font-style: italic;
        }
      }
    }
    &-icon{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: .9375em;
      right: .625em;
    }
  }
  &__popular{
    margin: 1.5em 0 .625em;
  }
}

.m-color{
  display: flex;
  align-items: center;
  &__view{
    width: 1em;
    height: 1em;
    flex-shrink: 0;
    @include center;
    margin-right: .5em;
    border-radius: .25em;
    svg{
      width: .5em;
      opacity: 0;
    }
  }
  label{
    font-size: .875em;
    color: $grayDark;
  }
  input:checked{
    &~label{
      color: $black;
    }
    &~.m-color__view{
      svg{
        opacity: 1;
      }
    }
  }
  &._white{
    .m-color__view{
      path{
        fill: $white;
      }
    }
  }
}
