.product{
  position: relative;
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .625em;
    }
  }
}

.product-item{
  position: absolute;
  &._open{
    .product-item__point{
      background-color: $primary;
      transform: rotate(-45deg);
      background-size: .875em auto;
    }
    .product-item__inner{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._bottom{
    .product-item__inner{
      top: unset;
      bottom: 100%;
      margin-bottom: .375em;
      left: -2.75em;
    }
  }
  &__point{
    width: 1.5625em;
    height: 1.5625em;
    background-color: $grayActive;
    border-radius: 50%;
    @include center;
    font-weight: 800;
    color: $white;
    background-image: url(../img/plus.svg);
    @include bgImgSize(.5em);
    transition: background-color .3s;
    &:hover{
      background-color: $primary;
    }
  }
  &__inner{
    position: absolute;
    background-color: $white;
    padding: .3125em;
    border-radius: 1.25em;
    width: 23.125em;
    right: -1.875em;
    top: 100%;
    margin-top: .3125em;
    display: grid;
    grid-template-columns: 6.25em calc(100% - 6.875em);
    gap: .625em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__img{
    @include imgCover(100%);
    border-radius: .625em;
  }
  &__top{
    padding: .5em 0;
  }
  &__action{
    position: absolute;
    top: .3125em;
    right: .3125em;
    display: flex;
    &-btn{
      width: 1.875em;
      height: 1.875em;
      margin-left: .25em;
      svg{
        width: 1.25em;
      }
    }
  }
  &__bottom{
    margin-top: .5em;
  }
  &__cart-btn{
    width: 1.875em;
    height: 1.875em;
    background-color: $gray;
    margin-left: 1em;
    svg{
      width: 1.25em;
    }
  }
}

