.about{
  padding: 5.75em 0 7.5em;
  margin: 3.125em 0;
  background-color: $gray;
  background-image: url(../img/about-bg.webp);
  background-size: cover;
  background-position: center;
  &__items{
    margin-top: 3.125em;
  }
}

.about-item{
  width: 18.75em;
  &__num{
    font-size: 4.75em;
    line-height: 164%;
    font-weight: 100;
  }
  &__title{
    font-size: 1.125em;
    line-height: 139%;
    font-weight: 600;
    margin-top: -.22em;
  }
  &__desc{
    margin-top: .15em;
    font-size: .875em;
    color: $grayDark;
    @include truncate(3);
  }
}

.ideas-slider{
  margin-top: 1.875em;
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: calc(100% - 33.75em) 32.75em;
        gap: 1.25em 1em;
        transform: unset !important;
      }
      &-slide{
        &:nth-child(1){
          grid-column: 1/2;
          grid-row: 1/4;
          .idea-card__img{
            padding-bottom: 43.5%;
          }
        }
        &:not(:first-child){
          .idea-card{
            display: grid;
            grid-template-columns: 15.875em calc(100% - 17.125em);
            gap: 1.25em;
            align-items: flex-start;
            &__img{
              padding-bottom: 63%;
            }
            &__content{
              margin: 0;
            }
            &__title{
              font-size: .875em;
            }
            &__desc{
              font-size: .875em;
              line-height: 100%;
              margin-top: .71em;
              @include truncate(8);
              color: $grayDark;
            }
            &__nav{
              padding-top: .625em;
            }
          }
        }
      }
    }
  }
}

.idea-card{
  display: flex;
  flex-direction: column;
  &__img{
    border-radius: .625em;
    @include imgCover(65.3%);
  }
  &__content{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    margin-top: 1.25em;
  }
  &__title{
    font-size: 1.5em;
    font-weight: 700;
  }
  &__desc{
    font-size: 1.1875em;
    line-height: 120%;
    margin-top: .79em;
    @include truncate(3);
  }
  &__nav{
    padding-top: 1.5em;
    margin-top: auto;
    display: flex;
  }
  &__link{
    font-size: .875em;
  }
}


