.sales{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.875em 1.25em;
    grid-auto-flow: dense;
    margin-top: 3.75em;
  }
  .pagination{
    margin-top: 3.125em;
  }
}

.sale-card{
  &._large{
    grid-column: 1/-1;
    .sale-card__img{
      height: 20.625em;
    }
  }
  &._medium{
    grid-column: span 3;
  }
  &__img{
    display: block;
    border-radius: 1.25em;
    height: 16em;
    @include imgCover(100%);
    padding-bottom: 0;
  }
  &__content{
    margin-top: 1em;
    @include grid100gap(.625em);
  }
  &__title{
    @include truncate(2);
    line-height: 100%;
  }
  &__desc{
    @include truncate(3);
    line-height: 100%;
  }
}

.sale-item{
  margin-top: 1.375em;
  .sale-card__title{
    font-size: 1.3125em;
  }
}

.sale-products{
  margin-top: 3.75em;
}

.sale-filter{
  display: flex;
  flex-wrap: wrap;
  margin-top: -.625em;
  .filter__all{
    margin: .625em .625em 0 0;
  }
}
