._cart-grid{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1.75em / 3));
  gap: .875em;
}

.cart-delivery{
  &__item{
    input:hover,
    input:checked{
      &~.delivery-tab{
        color: $primary;
        border-color: $primary;
      }
    }
  }
}

.cart-map{
  margin-top: 1.875em;
  position: relative;
  &__search{
    position: absolute;
    top: 1.25em;
    left: 1.25em;
    width: calc(100% - 2.5em);
    z-index: 2;
    &._show{
      .cart-map__search-hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    .fg{
      position: relative;
      z-index: 2;
      input{
        background-color: $white;
        padding-left: 9.71em;
      }
      label{
        pointer-events: none;
        position: absolute;
        left: 1.43em;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    &-hidden{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      padding: 4em 1em 1.875em 8.5em;
      background-color: $white;
      border-radius: .5em;
      border: $border;
      transition: all .3s;
      pointer-events: none;
      opacity: 0;
    }
    &-result{
      @include grid100gap(.5em);
    }
    &-item{
      font-size: .875em;
      line-height: 130%;
      &:hover{
        color: $primary;
      }
    }
  }
}

.delivery-tariff{
  margin-top: 1.875em;
  &__view{
    display: none;
    margin-top: 1.0625em;
    &._active{
      display: block;
    }
  }
  &__hint{
    padding: 1em 1.25em;
    border-radius: .625em;
    background-color: $primary;
    color: $white;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 8.875em;
      margin-top: -.5em;
      background-image: url(../img/triangle.svg);
      width: 1.5em;
      height: .75em;
      @include bgImgSize(100%);
    }
    &._type2{
      &:before{
        left: calc(100% / 3 - 1.75em / 3 + 8.875em + .875em);
      }
    }
  }
}

.radio-field{
  padding: .75em 1.25em;
  min-height: 4.375em;
  display: flex;
  align-items: center;
  border-radius: .625em;
  background-color: $gray;
  position: relative;
  &__check{
    width: 1.5em;
    height: 1.5em;
    flex-shrink: 0;
    border-radius: .4375em;
    border: $border;
    background-image: url(../img/check7.svg);
    @include bgImgSize(0);
    margin-right: .6875em;
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 1px solid $primary;
      border-radius: .625em;
      opacity: 0;
      pointer-events: none;
    }
  }
  &__icon{
    width: 1.375em;
    margin-right: .625em;
    &._big{
      width: 1.75em;
    }
  }
  input:checked{
    &~.radio-field__check{
      background-size: .6875em auto;
      background-color: $primary;
      border-color: $primary;
      &:after{
        opacity: 1;
      }
    }
  }
}

.delivery-service{
  margin-top: 3.125em;
  &__items{
    margin-top: 1.25em;
  }
  &__field{
    margin-top: 1.875em;
  }
}

.user-type{
  .radio-field__icon{
    width: 2.5em;
  }
  .radio-field{
    &__check{
      width: 0;
      height: 0;
      border: none;
      margin: 0;
    }
    input:checked{
      &~.radio-field__title{
        color: $primary;
      }
    }
  }
  &__hint{
    padding: .75em 0 0 .6875em;
    display: flex;
    align-items: center;
    color: $grayDark;
    a{
      text-decoration: underline;
    }
    img{
      width: .625em;
      margin-right: .5375em;
    }
  }
}

.requisites-upload{
  .file-input{
    &__btn{
      width: 16.5em;
      background-color: $white;
      box-shadow: $shadow;
      color: $grayDark;
    }
  }
}


