.about4{
  .about{
    padding: 4em 0;
    margin: 1.875em 0 0;
    &__items{
      margin: 0;
    }
  }
  &__top{
    text-align: center;
    width: 46.625em;
    max-width: 100%;
    margin: 0 auto 1.875em;
    @include grid100gap(1.875em);
  }
}

.welcome{
  padding: 10em 0;
  background-size: cover;
  background-position: center;
  margin-bottom: .3125em;
  &__inner{
    width: 26.5em;
    max-width: 100%;
    text-align: center;
    margin: 0 auto;
    .h1{
      font-weight: 900;
    }
  }
}

.cooperate{
  &__inner{
    padding: 2.75em;
    background-image: url(../img/cooperate-bg.webp);
    background-size: cover;
    background-position: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $gray;
    border-radius: 1.875em;
  }
  &__desc{
    width: 39.5em;
    max-width: 100%;
  }
  &__btn{
    margin-top: 1.625em;
  }
}

.assortment{
  margin: 2.5em 0;
  background-size: cover;
  background-position: center;
  padding: 3.75em 0;
  color: $white;
  min-height: 37.5em;
  &:after{
    background-color: rgba($black,.8);
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 11.25em / 4));
    gap: 3.75em;
    margin-top: 3.75em;
  }
  &__subtitle{
    margin: 10em auto 0;
    width: 54em;
    max-width: 100%;
    text-align: center;
    font-weight: 100;
    span{
      font-size: 3.5em;
    }
  }
}

.assortment-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &:hover{
    .assortment-item__btn{
      opacity: 1;
    }
  }
  &__num{
    font-size: 3.5em;
    font-weight: 100;
  }
  &__text{
    min-height: 3.125em;
    @include center;
    margin-top: 1em;
  }
  &__btn{
    opacity: 0;
    height: 2.375em;
    margin-top: 1.25em;
    span{
      font-size: .875em;
    }
  }
}

.opinion{
  &__top{
    @include grid100gap(1.875em);
  }
  &__items{
    margin-top: 2.5em;
    display: flex;
    justify-content: space-between;
  }
}

.rating-block{
  display: flex;
  align-items: center;
  &__num{
    display: flex;
    align-items: center;
    order: -1;
    font-size: 1.8125em;
    font-weight: 800;
    margin-right: .33em;
  }
}

.rating-block{
  &._disabled{
    pointer-events: none;
  }
}

.rating{
  cursor: pointer;
  &__stars{
    width: 8.25em;
    max-width: 100%;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__fill{
    width: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    overflow: hidden;
    img{
      width: 8.25em;
      display: block;
    }
  }
}

.site-item{
  &__img{
    height: 1.625em;
    display: flex;
    align-items: center;
    margin-bottom: .5em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}
