.filter-slider{
  &__item{
    min-height: .9375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: $grayActive;
    border: none;
    box-shadow: none;
    height: .25em;
    border-radius: .625;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: 1px solid $primary;
      background-color: $white;
      width: .9375em;
      height: .9375em;
      top: -.375em !important;
      right: -.375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__outputs{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: .375em;
    &-item{
      border-radius: .375em;
      background-color: $gray;
      padding: .3125em .625em;
    }
    &-title,
    &-num{
      display: inline-block;
      font-size: .8125em;
      line-height: 123%;
    }
    &-num{
      font-weight: 700;
    }
  }
}
