.cart{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 21.75em - 1.375em) 21.75em;
    gap: 1.375em;
    margin-top: 3.125em;
  }
  &__sticky{
    position: sticky;
    top: 1em;
    @include grid100gap(.625em);
  }
  &__group{
    margin-top: 3.75em;
    &:first-child{
      margin-top: 0;
    }
  }
  &__title{
    height: 2.875em;
    border-radius: .625em;
    background-color: $gray;
    @include center;
    margin-bottom: 1.875em;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__items{
    @include grid100gap(1.875em);
  }
}

.cart-sum,
.cart-info,
.cart-promo{
  background-color: $gray;
  border-radius: .625em;
}

.cart-info{
  padding: 1.25em;
  @include grid100gap(.625em);
  &__props{
    @include grid100gap(.25em);
  }
  .prop-item{
    &:not(.primary-text){
      color: $black;
    }
  }
}

.cart-promo{
  padding: .625em;
  .fg{
    input{
      background-color: $white;
    }
  }
}

.cart-sum{
  padding: 1.25em;
  @include grid100gap(.3125em);
  &__item{
    display: flex;
    justify-content: space-between;
  }
}

.cart-confirm{
  padding: .625em 0 0;
  .m-check{
    max-width: 17em;
    margin-left: 1.25em;
  }
  &__btn{
    height: 3.75em;
    width: 100%;
    margin-top: 1.75em;
    border-radius: .625em;
    font-weight: 700;
    span{
      font-size: 1em;
    }
  }
  &__nav{
    padding: 3.125em 1em 0;
    display: grid;
    grid-template-columns: 1fr 1.2fr;
    gap: 1em;
  }
  &__btn2{
    display: flex;
    align-items: flex-start;
    text-align: left;
    img{
      width: 1.875em;
      margin: .0625em .5em 0 0;
    }
    span{
      font-size: .8125em;
    }
    &:hover{
      opacity: .8;
    }
  }
}

.cart-card{
  display: grid;
  grid-template-columns: calc(100% - 19.6875em - 1.875em) 19.6875em;
  gap: 1.875em;
  border-radius: .625em;
  background-color: $white;
  box-shadow: $shadow;
  padding: 1em 1.25em;
  &._disabled{
    opacity: .2;
  }
  &__title{
    margin-top: .625em;
  }
  &__props{
    margin-top: .375em;
    @include grid100gap(.375em);
  }
  &__colors{
    margin-top: .9375em;
  }
  &__top{
    position: relative;
  }
  &__img{
    @include imgCover(59%);
    border-radius: .625em;
    background-color: $gray;
  }
  &__action{
    position: absolute;
    top: .75em;
    right: .75em;
    display: flex;
    &-btn{
      margin-left: .3125em;
    }
  }
  &__info{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: .625em;
  }
  &__prices{
    display: flex;
  }
  &__price,
  &__price2{
    font-size: .8125em;
    letter-spacing: 0.01em;
    margin-left: .8em;
  }
  &__price{
    font-weight: 700;
  }
  &__price2{
    color: $grayDark;
    text-decoration: line-through;
  }
  &__prices2{
    margin-top: .625em;
    display: flex;
    justify-content: center;
    .cart-card__price,
    .cart-card__price2{
      font-size: 1.125em;
      margin: 0 .3em;
    }
  }
  .cartcalc{
    margin: .625em auto 0;
  }
}

.cart-colors{
  margin-top: .875em;
  &._active{
    .cart-colors__btn svg{
      transform: rotate(-180deg);
    }
  }
  &__items{
    @include grid100gap(.375em);
  }
  &__item{
    display: flex;
    align-items: center;
    color: $grayDark;
    img{
      margin-right: 1em;
      width: 1.4375em;
      height: 1.4375em;
      display: block;
      object-fit: cover;
      border-radius: .3125em;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    cursor: pointer;
    &:hover{
      .cart-colors__btn{
        path{
          fill: $primary;
        }
      }
    }
  }
  &__hidden{
    margin-top: .375em;
    display: none;
  }
  &__btn{
    margin-left: 1em;
    width: 1.25em;
    @include center;
    svg{
      width: 100%;
    }
  }
}
