.delivery{
  padding-bottom: 1.25em;
  &__links{
    color: $grayActive;
    ._active{
      color: $black;
    }
  }
  &__tabs{
    margin: 3.75em 0;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 2.625em / 4));
    gap: .875em;
    &._type2{
      grid-template-columns: repeat(3,calc(100% / 3 - 1.75em / 3));
    }
  }
}

.delivery-tab{
  padding: 1.25em 1.625em;
  border: 1px solid #e9e9e9;
  border-radius: .875em;
  display: flex;
  cursor: pointer;
  transition: all .3s;
  &._active,
  &:hover{
    color: $primary;
    border-color: $primary;
  }
  &._active{
    pointer-events: none;
  }
  &__icon{
    width: 2.5em;
    flex-shrink: 0;
    margin-right: .3125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding-top: .0625em;
  }
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 119%;
  }
  &__title{
    font-weight: 700;
  }
  &__desc{
    color: $grayDark;
  }
}

.delivery-item{
  display: grid;
  grid-template-columns: 30.125em calc(100% - 32.875em);
  gap: 2.75em;
  &__col{
    padding-top: 1.25em;
  }
  &__group{
    margin-top: 2.5em;
    &:first-child{
      margin-top: 0;
    }
  }
  &__street{
    margin-top: .875em;
  }
  &__props{
    margin-top: .875em;
    @include grid100gap(.5em);
  }
  &__prop{
    display: grid;
    grid-template-columns: 6.25em calc(100% - 7em);
    gap: .75em;
    &-title,
    &-desc{
      font-size: .875em;
      line-height: 120%;
    }
    &-title{
      color: $grayActive;
    }
    &-line{
      width: 100%;
      height: 1px;
      background-color: $grayActive;
      margin-top: .5em;
    }
  }
  &__props2{
    margin-top: 1.625em;
    @include grid100gap(.3125em);
    .delivery-item__prop{
      grid-template-columns: 6.875em calc(100% - 6.875em - .75em);
    }
  }
  &__props3{
    margin-top: 1.625em;
    @include grid100gap(.3125em);
    .delivery-item__prop{
      grid-template-columns: 13.75em calc(100% - 14.5em);
    }
  }
  &__text{
    margin-top: .33em;
    font-size: .875em;
  }
  &__desc{
    margin-top: .625em;
  }
  &__map-btn{
    display: flex;
    margin-top: .875em;
    .line-btn{
      color: $black;
      margin-right: 1.375em;
      &:hover{
        color: $primary;
      }
      &:last-child{
        margin-right: 0;
      }
    }
  }
}

.map{
  height: 33.25em;
  border-radius: 1.25em;
  overflow: hidden;
}

.delivery-advantages{
  padding: 3.75em 2.1875em;
  background-color: $gray;
  border-radius: 1.25em;
  background-image: url(../img/delivery-advantages.webp);
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: space-between;
  &__item{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    img{
      flex-shrink: 0;
      height: 1.8125em;
      margin-bottom: 1.375em;
    }
  }
  &__title{
    font-weight: 500;
    font-size: 1.125em;
    line-height: 139%;
  }
}

.delivery-item2{
  margin-top: 2.5em;
  &:first-child{
    margin-top: 0;
  }
}

.delivery-item3{
  margin-top: 3.75em;
}

.delivery-req{
  .custom-req__img{

  }
  .custom-req__title{
    span{
      &:nth-child(1){
        text-align: right;
        padding-right: 2.18em;
      }
    }
  }
}
