@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/filter-slider";
.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 1.875em;
    height: 1.875em;
    @include lastM(0 1.25em 0 0);
    @include center;
    &:hover{
      rect{
        stroke: $primary;
      }
      path{
        fill: $primary;
      }
    }
  }
  &--gray{
    .socials__item{
      border-radius: 50%;
      background-color: $grayActive;
      &:hover{
        background-color: $primary;
        rect{
          stroke: $white;
        }
        path{
          fill: $white;
        }
      }
    }
  }
}

.cart-btn,
.compare-btn,
.favourite-btn{
  @include center;
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  background-color: $gray;
  svg{
    width: 1.3125em;
  }
  &:hover{
    background-color: $grayActive;
  }
  &._added{
    path{
      fill: $primary;
    }
  }
}

.favourite-btn{
  ._fill{
    opacity: 0;
  }
  &._added{
    path{
      fill: #FF326F;
    }
    ._fill{
      opacity: 1;
    }
  }
}

.cart-btn{
  background-color: $grayDark;
  background-image: url(../img/check.svg);
  @include bgImgSize(0);
  &:hover{
    background-color: $primary;
  }
  &._added{
    background-color: $primary;
    background-size: 1.25em auto;
    svg{
      opacity: 0;
    }
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba($black,.8);
    pointer-events: none;
    border-radius: inherit;
  }
}
