.pagination{
  margin-top: 5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__more{
    margin-bottom: 3.75em;
  }
  &__items{
    width: 100%;
    display: flex;
    justify-content: center;
  }
  &__item{
    height: 2.375em;
    min-width: 2.375em;
    padding: 0 .625em;
    border-radius: .25em;
    border: 1px solid $grayActive;
    color: $grayActive;
    @include center;
    margin: 0 .3125em;
    svg{
      width: 1.5em;
      height: auto;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $primary;
      border-color: $primary;
      path{
        fill: #fff;
      }
    }
    &._btn{
      padding: 0 .4375em;
      border: none;
      &:first-child{
        margin: 0 1.25em 0 0;
      }
      &:last-child{
        margin: 0 0 0 1.25em;
      }
    }
  }
}


