.catalog-item{
  &__top{
    margin-top: .5em;
  }
  .product-card__feedback{
    margin-left: 4.875em;
  }
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: 48.875em calc(100% - 48.875em - 3.75em);
    justify-content: space-between;
  }
  .gallery{
    &__img{
      padding-bottom: 58.82%;
    }
    &__small{
      margin-top: 1.25em;
      .gallery__img{
        padding-bottom: 59.4%;
      }
      .swiper{
        width: calc(100% + 1.375em);
        &-slide{
          padding-right: 1.375em;
          width: 20%;
        }
      }
    }
  }
  &__available{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    color: $green;
    img{
      width: 1.25em;
      margin-right: .0625em;
    }
  }
  &__prices{
    margin-top: .5em;
    display: flex;
    &-item,
    &-item2{
      font-size: 1.5em;
      letter-spacing: 0.01em;
      line-height: 120%;
    }
    &-item2{
      text-decoration: line-through;
      color: $grayActive;
    }
    &-sale{
      margin-left: .9375em;
      border-radius: .3125em;
    }
  }
  &__nav{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__btn{
    height: 3em;
    border-radius: .5em;
    span{
      font-size: 1.125em;
    }
  }
}


.timer{
  margin-top: 1.875em;
  padding: .9375em;
  background-color: $gray;
  border-radius: .625em;
  text-align: center;
  @include center;
  flex-direction: column;
  .countdown{
    margin-top: 1.25em;
  }
}

.countdown{
  display: flex;
}

.countdownProp{
  display: flex;
  &__item{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__num,
  ._dots{
    font-size: 1.5em;
    line-height: 120%;
    font-weight: 700;
  }
  ._dots{
    margin: 0 1.125em;
  }
  &__title{
    font-size: .75em;
    line-height: 120%;
    margin-top: .45em;
  }
}

.product-colors{
  margin-top: 1.875em;
  &__tabs{
    display: flex;
  }
  &__tab{
    margin-right: .25em;
    padding: 0 .6875em;
    display: flex;
    align-items: center;
    height: 1.75em;
    color: $grayDark;
    transition: all .3s;
    cursor: pointer;
    border-radius: .625em .625em 0 0;
    border: 1px solid transparent;
    border-bottom: none;
    span{
      font-size: .875em;
      line-height: 120%;
    }
    &:hover{
      color: $black;
    }
    &._active{
      pointer-events: none;
      border-color: $black;
      color: $black;
      font-weight: 700;
    }
  }
}
