.partners-slider{
  margin-top: 1.875em;
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      padding-right: 1.25em;
      width: calc(100% / 7);
    }
    &-button{
      &-prev{
        left: .625em;
      }
      &-next{
        right: .625em;
      }
    }
  }
}

.partner-card{
  height: 5.625em;
  border-radius: .625em;
  background-color: $gray;
  @include center;
  padding: 1.125em;
  img{
    mix-blend-mode: multiply;
    max-width: 100%;
    max-height: 100%;
  }
}

.ideas,
.portfolio{
  &__top{
    position: relative;
  }
  @media screen and (min-width: 993px){
    .m-link{
      position: absolute;
      right: 0;
      top: 0;
    }
  }
}

.m-link{
  display: flex;
  align-items: center;
  ._view2{
    display: none;
  }
  span{
    font-size: 1em;
    line-height: 125%;
    text-decoration: underline;
  }
  svg{
    width: .875em;
    margin-left: .25em;
  }
  &:hover{
    color: $primary;
    path{
      fill: $primary;
    }
  }
}

.portfolio-slider{
  margin-top: 1.25em;
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(5,calc(20% - .75em));
        gap: .625em .9375em;
        transform: unset !important;
      }
      &-slide{
        &:nth-child(1){
          grid-column: 1/4;
          grid-row: 1/4;
          .portfolio-card__img{
            padding-bottom: 72.6%;
          }
        }
      }
    }
  }
}

.idea-card,
.portfolio-card{
  position: relative;
  &:hover{
    &:before{
      opacity: 1;
    }
  }
  &:before{
    content: '';
    @include centerAbsolute;
    z-index: -1;
    height: calc(100% + .875em);
    width: calc(100% + .875em);
    background-color: $white;
    box-shadow: $shadow;
    border-radius: .625em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
}

.portfolio-card{
  display: block;
  &__img{
    @include imgCover(63%);
    border-radius: .375em;
  }
  &__top{
    height: 2.125em;
    display: flex;
    align-items: center;
    padding: 0 .625em;
    color: $grayDark;
  }
  &__title{
    @include truncate(2);
  }
}


@media screen and (max-width: 992px){
  .portfolio {
    &__top {
      align-items: flex-start;
      justify-content: unset;
    }
    .m-link{
      margin-top: .25em;
      margin-left: 1em;
      flex-shrink: 0;
    }
  }
}


