.advantages{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
    gap: 1.875em;
    margin-top: 1.875em;
  }
}

.advantage-card{
  position: relative;
  border-radius: 1.875em;
  &._big{
    grid-column: span 2;
    .advantage-card__img{
      padding-bottom: 48.7%;
    }
    .advantage-card__content{
      top: unset;
      bottom: 0;
      padding: 3.125em 3.125em 3.125em 9.625em;
    }
  }
  &__img{
    @include imgCover(102%);
    border-radius: 1.875em;
  }
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 2em 2em 2em 3.75em;
  }
  &__title{
    font-size: 1.625em;
    line-height: 96%;
    font-weight: 700;
  }
  &__desc{
    font-size: .9375em;
    line-height: 110%;
    margin-top: .67em;
    max-width: 15.33em;
  }
}

._mt30{
  margin-top: 1.875em;
}

.info{
  .text{
    column-count: 2;
    column-gap: 5em;
    color: $grayDark;
    p{
      margin-bottom: .6875em;
    }
  }
  &__more{
    display: flex;
    align-items: center;
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
    ._view2{
      display: none;
    }
    span{
      font-size: 1em;
      line-height: 125%;
    }
    svg{
      width: 1.25em;
      margin-left: .5em;
    }
  }
  &:not(._show-all){
    .text{
      p:nth-child(2){
        &~p{
          display: none;
        }
      }
    }
  }
  &._show-all{
    .info__more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      svg{
        transform: rotate(-180deg);
      }
    }
  }
}

.banners{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
}

.banner-card{
  display: block;
  position: relative;
  &:hover{
    .swiper-button{
      opacity: 1;
    }
  }
  &__img{
    @include imgCover(43.75%);
    border-radius: .9375em;
  }
  &__badge{
    z-index: 2;
    position: absolute;
    top: .625em;
    right: .625em;
    padding: 0 .625em;
    border-radius: .625em;
    height: 2.5em;
    background: rgba(255, 255, 255, 0.1);
    @include center;
    font-weight: 700;
    text-transform: uppercase;
    color: $white;
    span{
      font-size: 1em;
    }
  }
  .slider__wrap{
    border-radius: .625em;
  }
  .swiper-button{
    opacity: 0;
    &-prev{
      left: .625em;
    }
    &-next{
      right: .625em;
    }
    &.swiper-button-disabled{
      pointer-events: unset;
    }
  }
}


