.about-item2{
  margin-top: 1.875em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.625em));
  gap: 3.25em;
  &__list{
    margin-left: 2.125em;
    max-width: 31.875em;
    @include grid100gap(1.125em);
  }
  &__nav{
    padding: 2.1875em 0 0 3.875em;
    display: flex;
    .m-btn{
      padding: 0 1.5em;
      height: 2.5em;
      span{
        font-size: .875em;
      }
    }
  }
  &__img{
    @include imgCover(60%);
    border-radius: 1.875em;
    clip-path: polygon(100% 0, 100% 100%, 2.875em 100%, 0 calc(100% - 2.875em), 0 0);
  }
  &__promise{
    span{
      padding: .27em .5em;
      background-color: $black;
      color: $white;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      margin-top: .3125em;
    }
  }
  &._type2{
    align-items: center;
    .about-item2__img{
      clip-path: polygon(calc(100% - 2.875em) 0, 100% 2.875em, 100% 100%, 0 100%, 0 0);
    }
  }
}

.about3{
  &__top{
    text-align: center;
    @include grid100gap(.625em);
    max-width: 44.25em;
    margin: 0 auto;
  }
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}

.about-advantages{
  .advantage-card2{
    &:nth-child(1){
      clip-path: polygon(calc(100% - 2.875em) 0,100% 2.875em,100% 100%,0 100%,0 0);
    }
    &:nth-child(2){
      clip-path: polygon(2.875em 0%, 100% 0, 100% calc(100% - 2.875em), calc(100% - 2.875em) 100%, 0 100%, 0 2.875em);

    }
    &:nth-child(3){
      clip-path: polygon(100% 0,100% 100%,2.875em 100%,0 calc(100% - 2.875em),0 0);
    }
  }
}

.advantage-card2{
  padding: 2.25em;
  border-radius: 1.25em;
  background-color: $grayActive;
  color: $white;
  transition: all .3s;
  &:hover{
    background-color: $black;
  }
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: .75em;
  }
  &__icon{
    width: 1.3125em;
    height: 1.3125em;
    flex-shrink: 0;
    @include center;
    margin-right: 1.25em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 125%;
    font-weight: 700;
  }
  &__desc{
    font-size: 1em;
    line-height: 120%;
  }
}
