.header-middle{
  padding: 0 0 1.25em;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 13em;
    margin-right: 5.5em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.catalog-btn{
  height: 2.5em;
  padding: 0 1.25em;
  @include center;
  border-radius: .25em;
  margin-right: 1.25em;
  &__icon{
    width: 1.25em;
    height: 1.25em;
    flex-shrink: 0;
    position: relative;
    margin-right: .625em;
    img{
      width: 100%;
      display: block;
      @include centerAbsolute;
    }
  }
  &__title{
    font-size: 1em;
  }
}

.header-search{
  flex-grow: 1;
  margin-right: 1.25em;
  .fg{
    input{
      height: 2.86em;
      padding-left: 3.57em;
    }
    label{
      top: 1em;
      left: 3.35em;
    }
  }
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 1.25em;
    width: 1.25em;
  }
}

.header-action{
  display: flex;
  align-items: center;
  &__item{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: 2.5em;
    &:last-child{
      margin-right: 0;
    }
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.header-bottom{
  padding: 1em 0;
}

.nav2{
  .menu{
    display: flex;
    justify-content: space-between;
  }
  a{
    font-size: .875em;
    letter-spacing: .01em;
  }
}
