@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.25em;
    height: 2.75em;
    border-radius: .25em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.1875em;
        letter-spacing: 0.02em;
    }
}

.m-btn-small{
    height: 1.875em;
    span{
        font-size: .75em;
        letter-spacing: unset;
    }
}

.m-btn-small2{
    height: 1.875em;
    span{
        font-size: .875em;
        letter-spacing: unset;
    }
}


.m-btn-medium{
    height: 2.375em;
    padding: 0 .9375em;
    span{
        font-size: .875em;
        letter-spacing: unset;
    }
}

.m-btn-white-outline{
    border: 1px solid $white;
    color: $white;
    &:hover{
        color: $black;
        background-color: $white;
    }
}

.m-btn-gray-outline{
    border: 1px solid $grayActive;
    color: $grayDark;
    &:hover{
        color: $primary;
        border-color: $primary;
    }
}

.m-btn-black-outline{
    border: 1px solid $black;
    color: $black;
    &:hover{
        color: $primary;
        border-color: $primary;
    }
}

.m-btn-orange{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}

.line-btn{
    color: $grayDark;
    &:hover{
        color: $primary;
    }
    span{
        font-size: .875em;
        line-height: 120%;
        text-decoration: underline;
    }
}
