.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .875em;
    margin-bottom: .375em;
    color: $grayDark;
    span{
      color: $primary;
    }
  }
  input,
  textarea{
    border-radius: 0.57em;
    border: $border;
    padding: 0 1.43em;
    height: 3.28em;
    width: 100%;
    font-size: .875em;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: $grayDark;
    }
    &:focus {
      border-color: #9CA0A0;
      box-shadow: 0 .25em .4375em 0 rgba(47, 63, 67, 0.03), 0 .25em .3125em 0 rgba(0, 0, 0, 0.02) inset;
    }
    &:disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      color: rgba(#000,.4);
    }
  }
  textarea{
    padding-top: 1.07em;
    padding-bottom: 1.07em;
    height: 6.93em;
    resize: none;
  }
  &__field{
    position: relative;
  }
}

.fg-dynamic{
  label{
    position: absolute;
    top: 1.14em;
    left: 1.22em;
    padding: 0 .21em;
    transition: all .2s;
    pointer-events: none;
    background-color: $white;
  }
  textarea,
  select,
  input{
    &._filled,
    &:focus{
      &~label{
        top: 0;
        transform: translateY(-50%);
      }
    }
  }
  &._error{
    label{
      color: $primary;
    }
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1em;
  right: 1.25em;
  width: 1.25em;
  height: 1.6875em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    g{
      opacity: 1;
    }
  }
}

.radio-field,
.cart-delivery__item,
.file-input__btn,
.color-field,
.m-color,
.filter__all,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.5em;
    min-height: 1em;
    color: $grayDark;
    span{
      font-size: .875em;
      line-height: 114%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: $border;
      width: 1em;
      height: 1em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  input:checked~label{
    color: $black;
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: .5em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .25em;
      background-image: url(../img/check2.svg);
    }
  }
  &._type2{
    label{
      padding-left: 2.1875em;
      padding-top: .3125em;
      &:before{
        width: 1.5em;
        height: 1.5em;
        border-radius: .4375em;
        background-image: url(../img/check7.svg);
      }
    }
    input:checked~label{
      &:before{
        background-color: $primary;
        border-color: $primary;
        background-size: .6875em auto;
      }
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      background-size: .375em auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 8.75em;
  display: grid;
  grid-template-columns: 2.5em calc(100% - 2.5em * 2) 2.5em;
  height: 2.5em;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    color: $grayDark;
    font-weight: 500;
    font-size: 1.125em;
    text-align: center;
  }
  svg{
    width: .75em;
    height: auto;
  }
  button{
    border: $border;
    border-radius: .4375em;
    @include center;
    &:hover{
      background-color: #E6E6EC;
    }
    &._disabled{
      opacity: .6;
      pointer-events: none;
    }
  }
}


