.first-block{
  padding: 1.125em 0 3.125em;
}

.grid4{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - .9375em));
  gap: 1.875em 1.25em;
}

.favourite{
  padding-bottom: .625em;
  &__delete{
    padding: 0 1em;
    height: 2.25em;
    border-radius: 2.125em;
    span{
      font-size: .875em;
    }
  }
  .product-card{
    &__action{
      opacity: 1;
      &-btn{
        background-color: transparent;
      }
      .compare-btn{
        opacity: 0;
      }
    }
  }
}

.sort{
  select {
    width: auto;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    padding: 0 1em;
    margin-left: -1em;
    height: 2.125em;
    border: none;
    background-color: transparent;
    font-size: 1em;
    line-height: 140%;
    color: $grayDark;
    background-image: url(../img/seect-arrow.svg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: .625em auto;
    font-family: $font1;
    &::-ms-expand {
      display: none; // IE
    }
  }
}
