.text ul,
.m-list{
  li{
    padding-left: 1.75em;
    position: relative;
    font-size: 1em;
    line-height: 160%;
    &:before{
      content: '';
      position: absolute;
      background-image: url(../img/check4.svg);
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(100%);
      left: 0;
      top: .125em;
    }
  }
}

.text{
  h3{
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 700;
    margin-bottom: .7em;
  }
  p{
    font-size: 1em;
    line-height: 120%;
    margin-bottom: .6875em;
    &:last-child{
      margin-bottom: 0;
    }
  }
  img{
    width: 100%;
    display: block;
    border-radius: 1.25em;
  }
  a{
    text-decoration: underline;
    color: $primary;
    font-weight: 700;
    &:hover{
      text-decoration-color: transparent;
    }
  }
  li{
    font-size: 1em;
    line-height: 160%;
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: ' ' counter(list) '. ';
      }
    }
  }
}
