.reviews{
  &__top{
    @include grid100gap(.5em);
  }
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: calc(100% - 20.5em) 17.25em;
    gap: 3.25em;
  }
  &__items{
    @include grid100gap(1.875em);
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.625em;
  }
}

.review-card{
  padding: .9375em 1.5625em;
  border-radius: .625em;
  border: $border;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__user{
    display: flex;
    align-items: center;
    font-weight: 700;
    img{
      margin-right: .3125em;
      width: 1.0625em;
    }
  }
  .rating{
    padding: .1875em 0 0 1.375em;
  }
  &__text{
    margin-top: 1em;
    font-size: .875em;
    line-height: 130%;
    p{
      line-height: inherit;
    }
  }
  &__nav{
    display: flex;
    margin-top: .875em;
  }
  &__more{
    color: $grayDark;
    &:hover{
      color: $primary;
    }
    span{
      font-size: .8125em;
      line-height: 120%;
    }
    ._view2{
      display: none;
    }
  }
  &__images{
    display: grid;
    grid-template-columns: repeat(9,calc(100% / 9 - 5em / 9));
    gap: .625em;
    margin-top: .875em;
    position: relative;
    cursor: pointer;
  }
  &__img{
    &-wrap{
      @include imgCover(69.7%);
    }
    &-more{
      position: absolute;
      right: 0;
      top: 0;
      background: linear-gradient(-270deg, rgba(#fff,0) 0%, rgba(#fff,.8) 48.5%, rgba(#fff,1) 100%);
      width: calc(100% / 9 - 5em / 9);
      height: 100%;
      @include center;
      &:hover{
        color: $primary;
      }
    }
  }
  &:not(._show-all){
    .review-card__text{
      p{
        &:nth-child(1){
          @include truncate(3);
          &~*{
            display: none;
          }
        }
      }
    }
  }
  &._show-all{
    .review-card__more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}

.review-gallery{
  &__wrap{
    width: 49em;
    max-width: 100%;
    margin: 0 auto;
  }
  .gallery{
    &__img{
      padding-bottom: 70%;
      border-radius: 0;
    }
    &__small{
      margin-top: 1.25em;
      .swiper{
        width: calc(100% + .4375em);
        &-slide{
          width: calc(100% / 9);
          padding-right: .4375em;
        }
      }
    }
    .swiper-button{
      path{
        fill: $grayActive;
      }
    }
  }
}

.review-info{
  position: sticky;
  top: 1em;
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: .5em;
    .rating{
      margin-left: 1em;
    }
  }
  &__num{
    font-size: 1.375em;
    line-height: 118%;
    font-weight: 700;
  }
  &__desc{
    font-size: .875em;
    letter-spacing: 0.02em;
    color: $grayDark;
  }
  &__items{
    margin-top: 1.5em;
    @include grid100gap(.625em);
  }
  &__item{
    display: flex;
    align-items: center;
  }
  &__btn{
    width: 100%;
    margin-top: 1.5em;
    height: 2.5em;
    span{
      font-size: .875em;
    }
  }
}

.review-modal{
  &__wrap{
    width: 20em;
    max-width: 100%;
    margin: 0 auto;
  }
  .rating{
    margin: 1.75em auto 0;
    justify-content: center;
    &-input{
      &__item{
        width: 1.8125em;
      }
    }
  }
}

.review-form{
  margin-top: 2em;
  &__fields{
    @include grid100gap(.625em);
  }
  .file-input{
    margin-top: .75em;
    &__btn{
      background-color: $gray;
      padding: 0 1em;
      display: inline-flex;
      width: auto;
      height: 1.875em;
      color: $grayDark;
      span{
        font-size: 1em;
      }
    }
  }
  &__hint{
    margin-top: 1em;
    text-align: center;
    color: $grayDark;
  }
  &__btn{
    height: 2.5em;
    width: 100%;
    margin-top: 1.625em;
    border-radius: .5em;
    span{
      font-size: .875em;
    }
  }
}
