.contacts{
  padding: 3.75em 0 3.125em;
  &__grid{
    margin-top: 3.75em;
    display: grid;
    grid-template-columns: calc(100% - 44em) 42.5em;
    gap: 1.5em;
  }
  &__phone{
    display: inline-flex;
    align-items: center;
    font-weight: 700;
    svg{
      width: 2.1875em;
      margin-right: 1.5em;
    }
    span{
      font-size: 1.75em;
      line-height: 121%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  &__group{
    margin-top: 3.75em;
  }
}

.contacts-gallery{
  &__img{
    @include imgCover(79.4%);
    border-radius: 1.25em;
  }
  .slider__wrap{
    border-radius: 1.25em;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.5em);
    }
    &-slide{
      padding-right: 1.5em;
    }
    &-button{
      &-prev{
        left: 1.25em;
      }
      &-next{
        right: 1.25em;
      }
    }
  }
}

.contacts2{
  padding: 3.5em 0;
  background-color: $gray;
  margin: 3.125em 0 -1.875em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.25em;
  }
  &__links{
    padding: 1.25em 0 0 .625em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 .875em);
    display: flex;
    align-items: center;
    svg{
      width: 1.1875em;
      margin-right: .625em;
    }
    span{
      font-size: .875em;
      line-height: 120%;
    }
    &:hover{
      path:not(._stroke){
        fill: $primary;
      }
      ._stroke{
        stroke: $primary;
      }
    }
  }
}

.map-modal{
  padding: 0;
  &:after{
    display: none;
  }
  .modal{
    &__dialog{
      width: 100%;
      max-width: unset;
      border-radius: 0;
      height: $vh;
      overflow-y: auto;
      padding: 4.625em 0;
    }
  }
  .close-btn{
    top: -1.5625em;
    right: 1.25em;
    z-index: 2;
  }
  &__wrap,
  &__inner{
    position: relative;
  }
}

.contacts-info{
  position: absolute;
  width: 29.5em;
  padding: 1.25em;
  background-color: $white;
  box-shadow: $shadow;
  z-index: 3;
  border-radius: 1.25em;
  left: 6.25em;
  top: 50%;
  transform: translateY(-50%);
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__close{
    position: absolute;
    top: .625em;
    right: .625em;
    width: 1.375em;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  .delivery-item__group{
    margin: 0;
  }
}

.requisites{
  width: 42em;
  max-width: 100%;
  margin: 0 auto;
  position: relative;
  &__info{
    margin-top: 1.625em;
    @include grid100gap(.25em);
    p{
      font-size: .875em;
      line-height: 120%;
    }
  }
  &__props{
    margin-top: 1.25em;
    @include grid100gap(.5em);
  }
  &__print{
    position: absolute;
    right: 1.25em;
    bottom: 1.25em;
    display: flex;
    align-items: center;
    flex-direction: column;
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
    svg{
      width: 1.875em;
      margin-bottom: .1875em;
    }
  }
}
