._row-view{
  .catalog__items{
    grid-template-columns: 100%;
  }
  .product-card{
    display: grid;
    grid-template-columns: 19.6875em calc(100% - 20.9375em);
    gap: 1.25em;
    &:before{
      display: none;
    }
    &:hover{
      background-color: transparent;
    }
    &__col2{
      position: relative;
    }
    &__action{
      opacity: 1;
    }
    &__title{
      @include truncate(1);
    }
    &__arrow{
      display: none;
    }
    &__content{
      padding: .5em 0 2.5em;
      margin: 0;
      z-index: unset;
      gap: .9375em;
      position: static;
    }
    &__bottom{
      position: static;
      opacity: 1;
      pointer-events: unset;
      box-shadow: unset;
      background-color: transparent;
      padding: 0;
      &:after{
        display: none;
      }
    }
    &__props,
    &__colors{
      position: absolute;
      width: calc(100% - 15.5em);
      right: 0;
      top: 3.75em;
      margin: 0;
    }
    &__props{
      padding-top: 1.5em;
    }
    &__buy{
      position: absolute;
      left: 0;
      bottom: .5em;
    }
    &__cart-btn{
      position: absolute;
      left: 9.375em;
      bottom: .1875em;
      opacity: 1;
    }
  }
}

.cart-modal{
  .modal__dialog{
    padding: 1.5em;
    width: 28.75em;
    text-align: center;
  }
  &__title{
    margin: .3em 0;
  }
  &__desc{
    a{
      text-decoration: underline;
    }
  }
}

.config-modal{
  .modal__dialog{
    padding: 3.5em 2.75em;
    width: 67.625em;
  }
  .close-btn{
    top: 2.25em;
    right: 2.75em;
  }
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 19.6875em calc(100% - 20.9375em);
    gap: 1.25em;
    align-items: flex-start;
  }
  &__col2{
    padding-top: .5em;
  }
  &__wrap{
    margin-top: .9375em;
    display: grid;
    grid-template-columns: 13.75em calc(100% - 14.25em);
    gap: .5em;
    align-items: flex-start;
  }
  &__sub-col{
    @include grid100gap(.9375em);
  }
  &__colors{
    margin-top: 2.125em;
  }
  &__nav{
    display: flex;
    margin-top: 3.5em;
  }
  &__btn{
    padding: 0 1.75em;
    border-radius: .5em;
    span{
      font-size: 1.25em;
    }
  }
  .product-card{
    &__action{
      opacity: 1;
    }
    &__props{
      margin: 0;
    }
  }
}

.colors{
  display: flex;
  flex-wrap: wrap;
  .color-field{
    margin: .625em .625em 0 0;
  }
  &:not(._show-all){
    .color-field:nth-child(4)~.color-field{
      display: none;
    }
  }
  &._show-all{
    .colors__more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  &__more{
    width: 3.75em;
    height: 3.75em;
    border-radius: .625em;
    @include center;
    flex-shrink: 0;
    margin: .625em .625em 0 0;
    background-color: $gray;
    span{
      font-size: .75em;
    }
    ._view2{
      display: none;
    }
    &:hover{
      background-color: $primary;
      color: $white;
    }
  }
}

.color-field{
  width: 3.75em;
  flex-shrink: 0;
  position: relative;
  &:hover{
    .color-field__title{
      opacity: 1;
    }
  }
  &__img{
    @include imgCover(100%);
    border-radius: .625em;
  }
  &__title{
    z-index: 2;
    @include centerAbsolute;
    text-align: center;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    span{
      font-size: .6875em;
      line-height: 20%;
      -webkit-box-decoration-break: clone;
      box-decoration-break: clone;
      display: inline;
      background-color: $white;
      padding: .2em;
      border-radius: .4em;
    }
  }
  label{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: .625em;
    border: 2px solid $primary;
    background-image: url(../img/check3.svg);
    @include bgImgSize(1.5em);
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  input:checked~label{
    opacity: 1;
  }
}
